<template>
    <div class="echarts-views">
        <div class="echarts-header">
            <el-select v-model="value" filterable placeholder="请选择">
                <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
        </div>
        <div class="echarts-main" id="echartsMain"></div>
    </div>
</template>
<script>
import {city} from '../api/demo.js';
export default {
    data () {
        return {
            options: [],
            value: '',
            provinces: {
                //4个直辖市
                "北京": "beijing",
                "天津": "tianjin",
                "上海": "shanghai",
                "重庆": "chongqing",
                //22个省
                "河北": "hebei",
                "山西": "shanxi",
                "辽宁": "liaoning",
                "吉林": "jilin",
                "黑龙江": "heilongjiang",
                "江苏": "jiangsu",
                "浙江": "zhejiang",
                "安徽": "anhui",
                "福建": "fujian",
                "江西": "jiangxi",
                "山东": "shandong",
                "河南": "henan",
                "湖北": "hubei",
                "湖南": "hunan",
                "广东": "guangdong",
                "海南": "hainan",
                "四川": "sichuan",
                "贵州": "guizhou",
                "云南": "yunnan",
                "陕西": "shanxi1",
                "甘肃": "gansu",
                "青海": "qinghai",
                //5个自治区
                "新疆": "xinjiang",
                "广西": "guangxi",
                "内蒙古": "neimenggu",
                "宁夏": "ningxia",
                "西藏": "xizang",
                //3个特别行政区
                "香港": "xianggang",
                "澳门": "aomen",
                "台湾": "taiwan"
            },
            option: {
                backgroundColor: '#404a59',
                title : {
                    text: '中国地图',
                    subtext: '省市区三级下钻',
                    left: 'center',
                    textStyle:{
                        color: '#fff',
                        fontSize:16,
                        fontWeight:'normal',
                        fontFamily:"Microsoft YaHei"
                    },
                    subtextStyle:{
                        color: '#ccc',
                        fontSize:13,
                        fontWeight:'normal',
                        fontFamily:"Microsoft YaHei"
                    }
                },
                tooltip: {
                    trigger: 'item',
                    formatter: '{b}'
                },
                toolbox: {
                    show: true,
                    orient: 'vertical',
                    left: 'right',
                    top: 'center',
                    iconStyle:{
                        normal:{
                            color:'#fff'
                        }
                    }
                },
                animationDuration:1000,
                animationEasing:'cubicOut',
                animationDurationUpdate:1000
            },
            mapdata: []
        }
    },
    mounted () {
        this.getCityOptions()
        this.getProvince();
    },
    methods: {
        getCityOptions () {
            this.options = [];
            for (let i in this.provinces) {
                this.options.push({
                    label: i,
                    value: this.provinces[i]
                })
            }
            this.value = this.options[0].value;
        },
        getProvince () {
            city().then(res => {
                if (res.status === 200) {
                    let d = [];
                    res.data.features.forEach((item, index) => {
                        d.push({
                            name: item.properties.name
                        })
                    })
                    this.mapdata = d;
                    //注册地图
                    this.echarts.registerMap('china', res);
                    //绘制地图
                    this.renderMap('china', d);
                }
            })
        },
        renderMap (map, data){
            let chart = this.echarts.init(document.getElementById('echartsMain'))
            this.option.title.subtext = map;
            this.option.series = [ 
                {
                    name: map,
                    type: 'map',
                    mapType: map,
                    roam: false,
                    nameMap:{
                        'china':'中国'
                    },
                    label: {
                        normal:{
                            show:true,
                            textStyle:{
                                color:'#999',
                                fontSize:13
                            }  
                        },
                        emphasis: {
                            show: true,
                            textStyle:{
                                color:'#fff',
                                fontSize:13
                            }
                        }
                    },
                    itemStyle: {
                        normal: {
                            areaColor: '#323c48',
                            borderColor: 'dodgerblue'
                        },
                        emphasis: {
                            areaColor: 'skyblue'
                        }
                    },
                    data:data
                }	
            ];
            window.onresize = chart.resize;
            //渲染地图
            chart.setOption(this.option);
        }
    }
}
</script>
<style lang="less" scoped>
.echarts-views{
    width: 100%;
    height: 100%;
    position: relative;
    .echarts-header{
        line-height: 40px;
        height: 40px;
    }
    .echarts-main{
        width: 100%;
        height: calc(~'100% - 40px');
    }
}
</style>



